import React from 'react';
import PropTypes from 'prop-types';
import {FormDown, FormUp} from "grommet-icons";
import "styles/full-page/NetworkDropdown.css";
import {Checkmark} from "grommet-icons/es6";

export class NetworkDropdown extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            currentNetwork: this.props.currentNetwork,
            networkDropdownOpen: false,
            listOfNetworks: ["Toplnet", "Valhalla", "Private", "Local"]
        }
        this.wrapperRef = React.createRef();
        this.toggleNetworkDropdown = this.toggleNetworkDropdown.bind(this);
        this.switchNetwork = this.switchNetwork.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }

    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    toggleNetworkDropdown() {
        this.setState({networkDropdownOpen: !this.state.networkDropdownOpen});
    }

    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.current.contains(event.target)) {
            this.setState({networkDropdownOpen: false});
        }
    }

    switchNetwork(network) {
        this.setState({currentNetwork: network});
    }

    renderDropdown() {
        return(
            <ul className={"network-dropdown"}>
                <h1 className={"dropdown-title"}> Networks </h1>
                {this.state.listOfNetworks.map((network) => {
                    if (network === this.state.currentNetwork) {
                        return (
                            <button className={"dropdown-item"}>
                                {network}
                                <Checkmark color="black" style={{float: "right", height: "17px"}}/>
                            </button>);
                    }else{
                        return (<button className={"dropdown-item"} onClick={()=> this.switchNetwork(network)}>{network}</button>)
                    }
                })}
            </ul>
        );
    }

    render() {
        let buttonStyle, arrow;
        if (this.state.networkDropdownOpen) {
            buttonStyle = "network-button-selected";
            arrow = <FormUp color={"#FFFFFF"} style={{float: "right"}}/>;
        }else{
            buttonStyle = "network-button";
            arrow = <FormDown color="#26A69A"  style={{float: "right"}}/>;
        }
        return (
            <div ref={this.wrapperRef}>
                <button className={buttonStyle} onClick={this.toggleNetworkDropdown}>
                    {this.state.currentNetwork}
                    {arrow}
                </button>
                {(this.state.networkDropdownOpen) ? this.renderDropdown() : null}
            </div>
        );
    }


}

NetworkDropdown.propTypes = {
    currentNetwork: PropTypes.string.isRequired
}