import React from "react";
import 'styles/keys-page/KeysPage.css'
import PropTypes from "prop-types";
import KeyCard from "./KeyCard";
import {BsPlusCircle} from "react-icons/all";
import NewKeyfilePopup from "./NewKeyfilePopup";
import {Download} from "grommet-icons";
import ImportKeyfilePopup from "./ImportKeyfilePopup";
import MnemonicPhrasePopup from "./MnemonicPhrasePopup";


const fakeMnemonicPhrase =
    "secret portion force rebuild often grow fall carbon zebra van palm bar typical enter robot stand earth guess employ goose aisle great next embark"

/**
 * props:
 * currentKeyFile: publicAddr of current key file
 * array of keyFiles
 * (keyFile = map of public address to array of assets (boxType, balance)
 */
export class KeysPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showNewKeyfilePopup: false,
            showMnemonicPhrasePopup: false,
            showImportKeyfilePopup: false,
            keyFilesMap: this.props.keyFiles,
            newKeyfileInfo: {},
            newPubAddr: "6sYyiTguyQ455w2dGEaNbrwkAWAEYV1Zk6FtZMknWDKQ"   //Todo: create a public address for new key file.
        }
        this.toggleNewKeyfilePopup = this.toggleNewKeyfilePopup.bind(this);
        this.toggleImportKeyfilePopup = this.toggleImportKeyfilePopup.bind(this);
        this.addNewKeyfile = this.addNewKeyfile.bind(this);
        this.toggleMnemonicPhrasePopup = this.toggleMnemonicPhrasePopup.bind(this);
        this.saveKeyFileInfo = this.saveKeyFileInfo.bind(this);
    }

    toggleNewKeyfilePopup() {
        this.setState({showNewKeyfilePopup: !this.state.showNewKeyfilePopup});
    }

    toggleImportKeyfilePopup() {
        this.setState({showImportKeyfilePopup: !this.state.showImportKeyfilePopup});
    }

    toggleMnemonicPhrasePopup() {
        this.setState({showMnemonicPhrasePopup: !this.state.showMnemonicPhrasePopup});
    }

    /**
     * @param keyfileInfo - should have name, password, list of assets.
     */
    saveKeyFileInfo(keyfileInfo) {
        this.setState({newKeyfileInfo: keyfileInfo});
    }

    addNewKeyfile() {
        this.setState( {
            keyFilesMap: this.state.keyFilesMap.set(this.state.newPubAddr, this.state.newKeyfileInfo)
        });
    }

    render() {
        return(
            <div className={"keys-outer-gray"}>
                <h1>My Keys</h1>
                <div style={{display: "flex", flexWrap: "wrap"}}>
                    <div className={"add-key-card"}>
                        <button className={"button-style"} onClick={this.toggleNewKeyfilePopup}>
                           <BsPlusCircle className={"plus-sign"} />
                           <h1 style={{verticalAlign: "middle", margin: "0"}}> Add new keyFile </h1>
                        </button>
                        <span className={"or-style"}> OR </span>
                        <button className={"button-style"} onClick={this.toggleImportKeyfilePopup}>
                            <Download color="black" style={{float: "left"}}/>
                            <h1 style={{verticalAlign: "middle", margin: "0"}}> Import keyFile </h1>
                        </button>
                    </div>
                    {Array.from(this.state.keyFilesMap).map( ([pubAddr, info]) => {
                        if (pubAddr === this.props.currentKeyFile) {
                            return <KeyCard publicAddress={pubAddr} keyFileInfo={info}
                                            switchKeyFiles={this.props.switchKeyFiles} unlocked={true} />;
                        }else{
                            return <KeyCard publicAddress={pubAddr} keyFileInfo={info}
                                            switchKeyFiles={this.props.switchKeyFiles}/>;
                        }})
                    }
                </div>
                {this.state.showNewKeyfilePopup
                    ? <NewKeyfilePopup toggle={this.toggleNewKeyfilePopup} mnemonicPhraseToggle={this.toggleMnemonicPhrasePopup}
                                       saveKeyFileInfo={this.saveKeyFileInfo}/>
                    : null}
                {this.state.showMnemonicPhrasePopup
                    ? <MnemonicPhrasePopup toggle={this.toggleMnemonicPhrasePopup} mnemonicPhrase={fakeMnemonicPhrase}
                                           createKeyFile={this.addNewKeyfile}/>
                    : null}
                {this.state.showImportKeyfilePopup
                    ? <ImportKeyfilePopup toggle={this.toggleImportKeyfilePopup} addKeyfile={this.addNewKeyfile}/>
                    : null}
            </div>
        )
    }
}

KeysPage.propTypes = {
    currentKeyFile: PropTypes.string.isRequired,
    keyFiles: PropTypes.instanceOf(Map).isRequired,
    switchKeyFiles: PropTypes.func.isRequired
}