import React from 'react';
import {StatusGood} from "grommet-icons";
import PropTypes from "prop-types";
import CloseButton from "components/CloseButton";

export default class ConfirmationPopup extends React.Component {
    render() {
        return (
            <div className={"popUp"} style={{padding: "15px 15px 0 15px"}}>
                <CloseButton handleClose={this.props.toggle} style={{marginTop: "5px", marginRight: "20px"}}/>
                <StatusGood size="large" color={"#04844B"} style={{float: "left", paddingLeft: "10px", paddingRight: "10px"}}/>
                <h1 style={{margin: "0", paddingBottom: "20px"}}> Transaction has been broadcasted! </h1>
                <span style={{display: "block", paddingBottom: "10px"}}> Transaction Hash: </span>
                <span className={"tx-hash"}>  {this.props.txHash} </span>
            </div>
        )
    }
}

ConfirmationPopup.propTypes = {
    toggle: PropTypes.func.isRequired,
    txHash: PropTypes.string.isRequired
}