import React from "react";
import "styles/balances-page/ReceivePopup.css"
import PropTypes from "prop-types";
import CloseButton from "../CloseButton";
import CopyButton from "../CopyButton";

/**
 * Props:
 * public key: the public key of the keyfile currently opened.
 */

export default class ReceivePopup extends React.Component {

    render() {
        return (
            <div className={"receive-tab"}>
                <CloseButton handleClose={this.props.toggleReceive} />
                <div className={"field"}>
                    <span style={{float: "left"}}> Your public address: </span>
                    <div className={"address-box"} id={"address"} style={{float: "left"}}>
                        {this.props.publicAddress}
                    </div>
                    <div style={{float: "left"}}>
                    <CopyButton  elementIDToCopy={"address"}/>
                    </div>
                </div>
            </div>
        )
    }
}

ReceivePopup.propTypes = {
    publicAddress: PropTypes.string.isRequired,
    toggleReceive: PropTypes.func.isRequired
}