import React from "react";
import selectedSwitch from "images/switch-selected.svg";
import selectedDisabled from "images/selected-disabled.svg";
import PropTypes from "prop-types";
import "styles/balances-page/send/SendOffline.css";
import "styles/balances-page/send/SendPopup.css";
import "styles/ToplButtons.css";
import {LinkNext} from "grommet-icons";
import ReactTooltip from "react-tooltip";
import CopyButton from "components/CopyButton";

const fakeSignedTx = "0xf86b0284ee6b2800825208944bbeeb066ed09b7aed07bf39eee0460dfa26152088016345785d8a00008029a03ba7a" +
    "0cc6d1756cd771f2119cf688b6d4dc9d37096089f0331fe0de0d1cc1254a02f7bcd19854c8d46f8de09e457aec25b127ab4328e1c0d24bfb" +
    "ff8702ee1f474";

/**
 * Online steps: txStep: 1. generateTx, 2. signTx, 3. sendTx, 4. successfulTx
 * Offline steps: 1. importJson (for tx), 2. exportSignedTx
 */
export default class SendOffline extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            nonce: null,
            data: null,
            txStep: "generateTx",
            signedTx: "",
            signedTxError: false,
            generateTxError: false
        }
        this.exportSignTx = this.exportSignTx.bind(this);
        this.signTx = this.signTx.bind(this);
        this.continueToSign = this.continueToSign.bind(this);
        this.continueToSend = this.continueToSend.bind(this);
        this.successMsg = this.successMsg.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    /**
     * Continue to export signTx - for user to export signTx (from offline computer)
     */
    exportSignTx() {
        this.props.toggleNext();
        this.setState({txStep: "exportSignedTx"});
    }


    /**
     * For user to import signed tx
     */
    continueToSign() {
        this.props.confirmTx();
        this.props.toggleNext();
        this.setState({txStep: "signTx"});
    }

    /**
     * User has already exported tx and now back on online computer.
     */
    signTx() {
        this.props.toggleNext();
        this.setState({txStep: "signTx"});
    }


    continueToSend() {
        if (this.state.signedTx === "") {
            this.setState({signedTxError: true});
        }else{
            this.setState({txStep: "sendTx"});
        }
    }

    successMsg() {
        this.setState({txStep: "successfulTx"});
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    renderRecipients() {
        return (this.props.recipients.map(recipient => {
            return (
                <div>
                    <div className={"tx-row"}>
                        <span className={"field-title"}> Receiver: </span>
                        <span className={"field-value"}> {recipient.address} </span>
                    </div>
                    <div className={"tx-row"}>
                        <span className={"field-title"}> Amount: </span>
                        <span className={"field-value"}> {recipient.amount} </span>
                    </div>
                </div>
            )}));
    }

    render() {
        let render;
            if (this.props.online)  {
                render = (
                    <div>
                        <div className={"field"} style={{paddingBottom: "15px"}}>
                            <span> Nonce: </span>
                            <span className={"number"}> 0 </span>
                        </div>
                        <div className={"field"} style={{paddingBottom: "25px"}}>
                            <span> Fee: </span>
                            <span className={"number"}> 0 </span>
                        </div>
                        <span className={"field"}>
                            Send offline
                            <img src={selectedSwitch} className="offline-switch" alt="offline-switch"
                                 onClick={this.props.toggleSendOffline}/>
                        </span>
                        <div>
                            <button className={"blue-square-button"} style={{fontSize: '14px', fontWeight: "600", float: "none", margin: "20px 10px 20px 20px"}}> Export JSON File</button>
                            <button className={"text-button"} onClick={this.signTx}> Already have signed tx </button>
                            <button className={"blue-square-button"} onClick={this.continueToSign}> Continue</button>
                        </div>
                    </div>
                )
            }else {
                render = (
                    <div>
                        <button className={"blue-square-button"} style={{fontSize: '14px', fontWeight: "600", float: "none", margin: "20px"}}> Import JSON File</button>
                        <div className={"field"}>
                            <span> Data: </span>
                            <input type={"text"} name={"data"} placeholder={"0x"} value={this.state.data}
                                   className={"amount-box"} style={{width: "460px"}} onChange={this.handleChange}/>
                        </div>
                        <div className={"field"}>
                            <span> Nonce: </span>
                            <input type={"number"} name={"nonce"} placeholder={0} value={this.state.nonce}
                                   className={"amount-box"} style={{width: "447px"}} onChange={this.handleChange}/>
                        </div>
                        <div className={"field"}>
                            <span> Fee: </span>
                            <span className={"number"}> 0 </span>
                        </div>
                        <span className={"field"}>
                            Send offline
                            <img src={selectedDisabled} alt="offline-switch" style={{marginLeft: "15px", verticalAlign: "middle", width: "45px"}}
                                 data-tip="" data-for={"offlineTip"} />
                            <ReactTooltip id={"offlineTip"} place={"right"} effect={"solid"} className={"disconnected-tooltip"}>
                                No internet connection.
                            </ReactTooltip>
                        </span>
                        <div>
                            <button className={"blue-square-button"} style={{float: "none", marginLeft: "40%", fontWeight: "500", width: "180px"}}
                                    onClick={this.exportSignTx}>
                                Sign Transaction
                            </button>
                        </div>
                    </div>
                );
            }

        if (this.state.txStep === "exportSignedTx") {
         render = (
             <div>
                 <h1 style={{paddingLeft: "10px", fontSize: "20px"}}> Signed Transaction </h1>
                 <div className={"sign-tx-gray-box"} id={"signedTransaction"}>
                     {fakeSignedTx}
                 </div>
                 <div style={{float: "left", marginRight: "10px"}}> <CopyButton elementIDToCopy={"signedTransaction"}/> </div>
                 <button className={"blue-square-button"} style={{float: "none", fontSize: "16px"}}> Download JSON </button>
             </div>
         )

        } else if (this.state.txStep === "signTx") {
            let textArea = "signed-tx-input";
            if (this.state.signedTxError) {
                textArea = "signed-tx-error";
            }
            render = (
                <div className={"signed-tx"}>
                    Signed Transaction:
                    <textarea name={"signedTx"} value={this.state.signedTx} className={textArea} onChange={this.handleChange}/>
                    {this.state.signedTxError ? <span className={"error-message"}> Must import or paste in signed transaction. </span>
                    : null}
                    <button className={"import-button"}>
                        Import JSON File
                    </button>
                    <button className={"blue-square-button"} onClick={this.continueToSend}> Continue</button>
                </div>
            );
        } else if (this.state.txStep === "sendTx") {
            render = (
                <div style={{paddingTop: "15px", paddingLeft: "15px", fontWeight: "bold"}}>
                    Confirm Transaction:
                    <div className={"tx-row"}>
                        <span className={"field-title"}> Sender: </span>
                        <span className={"field-value"}> {this.props.fromAddress} </span>
                    </div>
                    {this.renderRecipients}
                    <div className={"tx-row"}>
                        <span className={"field-title"}> Nonce: </span>
                        <span className={"field-value"}> NaN </span>
                    </div>
                    <div className={"tx-row"}>
                        <span className={"field-title"}> Data: </span>
                        <span className={"field-value"}> 0x </span>
                    </div>
                    <div className={"tx-row"}>
                        <span className={"field-title"}> Fee: </span>
                        <span className={"field-value"}> 0 </span>
                    </div>
                    <button className={"blue-square-button"} onClick={this.successMsg}>
                        Send
                        <LinkNext color={"white"} style={{paddingLeft: "10px", verticalAlign: "middle"}}/>
                    </button>
                </div>
            )
        } else if (this.state.txStep === "successfulTx") {
            this.props.showConfirmation();
        }

        return <div> {render} </div>
    }
}

/**
 * Props:
 * toggleSendOffline: function - toggles between send offline and online
 * toggleNext: function - moving to the sign step - no longer need input fields.
 * fromAddress: string - the current pub address of the user
 * recipients: list of recipients and amount to send.
 * confirmTx: function to check if the "amount" and "to" fields have been filled in correctly.
 * showConfirmation: function to show a pop-up confirming the transaction.
 */
SendOffline.propTypes = {
    online: PropTypes.bool.isRequired,
    toggleSendOffline: PropTypes.func.isRequired,
    toggleNext: PropTypes.func.isRequired,
    fromAddress: PropTypes.string.isRequired,
    recipients: PropTypes.arrayOf(PropTypes.shape({
        address: PropTypes.string,
        amount: PropTypes.number
    })).isRequired,
    currentBalance: PropTypes.number.isRequired,
    confirmTx: PropTypes.func.isRequired,
    showConfirmation: PropTypes.func.isRequired
}