import React from 'react';
import PropTypes from "prop-types";
import {Down, Key, Money, Up} from "grommet-icons";
import BalancesSideBar from "./BalancesSideBar";
import topl from "images/topl-logo.png";
import toplLeaves from "images/topl-leaves.png"
import "styles/full-page/LeftMenuBar.css"
import ReactTooltip from "react-tooltip";
import {BiWallet} from "react-icons/all";

/**
 * props:
 * myBalancesOpen: boolean - indicates whether balances page or keys page is open.
 * walletTabOpen: boolean - indicates whether the wallet dropdown is open.
 * expandedSideBar: boolean - indicates whether the side bar is expanded or collapsed.
 * assets: array of assets (balance, boxType)
 * openWalletMenu: function - that toggles the wallet dropdown.
 * openBalances: function - opens balances page
 * openKeys: function - opens keys page
 */
export default class LeftMenuBar extends React.Component {

    renderExpandedSideBar() {
        let leftMenuItems;
        let balancesStyle = "sub-menu-item-open";
        let keysStyle = "sub-menu-item";

        //Show keys page
        if (!this.props.myBalancesOpen) {
            balancesStyle = "sub-menu-item";
            keysStyle = "sub-menu-item-open";
        }

        if (!this.props.walletTabOpen) {
            leftMenuItems = (
                <div>
                    <button onClick={this.props.openWalletMenu} className={"menu-button-clicked"} style={{width: "256px"}}>
                        <BiWallet className={"menu-icon"}/>
                        <span className={"menu-item"}>Wallet</span>
                        <Down color="black" style={{float: "right", paddingRight: "20px"}}/>
                    </button>
                </div>
            )
        }
        //expand sub-menu items
        else {
            leftMenuItems = (
                <div>
                    <button onClick={this.props.openWalletMenu} className={"menu-button-clicked"} style={{width: "256px"}}>
                        <BiWallet className={"menu-icon"}/>
                        <span className={"menu-item"}>Wallet</span>
                        <Up color="black" style={{float: "right", paddingRight: "20px"}}/>
                    </button>
                    <div>
                        <button className={balancesStyle} onClick={this.props.openBalances}> My Balances</button>
                        <ul className={"balances-list"}>
                            <BalancesSideBar  arbitsBalance={this.props.arbitsBalance} polysBalance={this.props.polysBalance}/>
                        </ul>
                        <button className={keysStyle} onClick={this.props.openKeys}> My Keys</button>
                    </div>
                </div>
            )
        }
        return(
            <div className={"left-menu"}>
                <img src={topl} className="topl-logo" alt="App-logo"/>
                <div className="left-menu-items">
                    {leftMenuItems}
                </div>
            </div>
        )

    }

    renderCollapsedSideBar() {
        let balancesColor, keyColor;
        if (this.props.myBalancesOpen) {
            balancesColor= "#26A69A";
            keyColor = "#444444";
        }else{
            balancesColor="#444444";
            keyColor="#26A69A";
        }
        return(
            <div className={"collapsed-left-menu"}>
                <img src={toplLeaves} className="topl-small-logo" alt="App-logo"/>
                <div className={"collapsed-menu-items"}>
                    <button className={"menu-button-clicked"} style={{width: "60px"}}>
                        <BiWallet className={"menu-icon"} style={{paddingLeft: "10px"}}/>
                    </button>
                    <button data-tip="" data-for={"balancesTip"} onClick={this.props.openBalances} className={"sub-buttons"}>
                        <Money color={balancesColor}/>
                    </button>
                    <button data-tip="" data-for={"keysTip"} onClick={this.props.openKeys} className={"sub-buttons"}>
                        <Key color={keyColor} />
                    </button>
                    <ReactTooltip id={"balancesTip"} place={"right"} effect={"solid"} arrowColor={"transparent"}>
                        My Balances
                    </ReactTooltip>
                    <ReactTooltip id={"keysTip"} place={"right"} effect={"solid"} arrowColor={"transparent"} data-offset={{'top': 10, 'left': 10}}>
                        My Keys
                    </ReactTooltip>
                </div>
            </div>
        )
    }


    render() {
        if (this.props.expandedSideBar) {
            return (
                <div>
                    {this.renderExpandedSideBar()}
                </div>);
        }else{
            return (
                <div>
                    {this.renderCollapsedSideBar()}
                </div>);
        }
    }
}

LeftMenuBar.propTypes = {
    myBalancesOpen: PropTypes.bool.isRequired,
    walletTabOpen: PropTypes.bool.isRequired,
    expandedSideBar: PropTypes.bool.isRequired,
    openWalletMenu: PropTypes.func.isRequired,
    openBalances: PropTypes.func.isRequired,
    openKeys: PropTypes.func.isRequired,
    polysBalance: PropTypes.number.isRequired,
    arbitsBalance: PropTypes.number.isRequired
}