import React from "react"
import 'styles/balances-page/WalletSummary.css';
import {Balance} from "./Balance";
import PropTypes from "prop-types";
import {AssetTable} from "./assets/AssetTable";

/**
 * props:
 * public address : string
 * array of assets (boxType, balance)
 */
export class WalletSummary extends React.Component{
    constructor(props) {
        super(props);
        this.state={
            assetsList: [
                {name: "Token 1", issuer: "3mBVXE3fuVfu1MBmMsBiRd6y9XokiVV591N65tBfA3FEvmqWB8", balance: 0, id: "1"},
                {name: "Token 2", issuer: "4EoSC4YmTm7zoPt5HDJU4aa73Vn2LPrmUszvggAPM5Ff3R1DVt", balance: 0, id: "2"}
            ]
        }
    }

    render() {
        const polysBalance = this.props.assets.filter(asset => asset.boxType === "poly")
            .reduce(function (a, b) {return a + b.balance}, 0);

        const arbitsBalance = this.props.assets.filter(asset => asset.boxType === "arbit")
            .reduce(function (a, b) {return a + b.balance}, 0);

        let balanceList =
            [
                <Balance publicAddress={this.props.publicAddress} balance={polysBalance} boxType={"poly"} online={this.props.online}/>,
                <Balance publicAddress={this.props.publicAddress} balance={arbitsBalance} boxType={"arbit"} online={this.props.online}/>
            ];

        return (
            <div className={"outer-gray"}>
                <div className={"inner-box"}>
                    <div className={"table-title"}> Balances </div>
                    {this.props.online
                        ? null
                        : <span style={{color: "red"}}>
                            *Balances could not be updated because there is no internet connection.
                        </span>
                    }
                    {balanceList}
                </div>

                <div className={"inner-box"}>
                    <div className={"table-title"}>
                        Other Assets
                    </div>
                    <AssetTable assetsList={this.state.assetsList} currentAddress={this.props.publicAddress} online={this.props.online}/>
                </div>
            </div>
        )
    }
}

WalletSummary.propTypes = {
    publicAddress: PropTypes.string.isRequired,
    assets: PropTypes.arrayOf(PropTypes.shape({
        boxType: PropTypes.string,
        balance: PropTypes.number
    })).isRequired,
    online: PropTypes.bool.isRequired
}


