import React from "react"
import PropTypes from "prop-types";
import "styles/full-page/BalancesSideBar.css"

/**
 * Props:
 * assets: (boxType, balance)
 */
export default class BalancesSideBar extends React.Component {

    render() {
        let balanceList = [
            (<ul className={"balances-menu-item"}> Polys
                <span className={"balance-amount"} style={{paddingLeft: "83px"}}> {this.props.polysBalance} </span>
            </ul>),
            (<ul className={"balances-menu-item"}> Arbits
                <span className={"balance-amount"} style={{paddingLeft: "80px"}}> {this.props.arbitsBalance} </span>
            </ul>)
        ];

        return (
            <div>
                {balanceList}
            </div>
        )
    }
}

BalancesSideBar.propTypes = {
    polysBalance: PropTypes.number.isRequired,
    arbitsBalance: PropTypes.number.isRequired
}