import React from 'react';
import PropTypes from "prop-types";
import "styles/keys-page/UnlockPopup.css";
import "styles/ToplButtons.css";
import CloseButton from "../CloseButton";

export default class UnlockPopup extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            password: "",
            showWarning: false
        }
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.setState({
            password: event.target.value,
            showWarning: false
        });
    }


    checkPassword(password) {
        if (password === this.props.password) {
            this.props.unlockKeyFile();
            this.props.toggle();
        }else{
            this.setState({showWarning: true});
        }
    }

    render() {
        let passwordBox = "password-box";
        if (this.state.showWarning) {
            passwordBox = "password-box-warning";
        }
        return (
            <div className={"unlock-popup"}>
                Unlock {this.props.walletName}
                <CloseButton handleClose={this.props.toggle} />
                <div className={"password-field"}>
                    <span> Password: </span>
                    <input type="password" name={"password"} className={passwordBox} placeholder={"Enter password"} value={this.state.password}
                           onChange={this.handleChange}/>
                </div>
                {(this.state.showWarning) ? <span className={"wrong-password"}> Wrong password </span> : null}
                <div style={{paddingTop: "15px"}}>
                    <button className={"round-blue-button"} onClick={() => this.checkPassword(this.state.password)}>
                        Unlock
                    </button>
                </div>
            </div>
        );
    }
}

UnlockPopup.propTypes = {
    walletName: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    toggle: PropTypes.func.isRequired,
    unlockKeyFile: PropTypes.func.isRequired
}