import React from 'react';
import PropTypes from "prop-types";
import {DownloadOption, Send} from "grommet-icons";
import ReactTooltip from "react-tooltip";
import SendPopup from "../send/SendPopup";
import ReceivePopup from "../ReceivePopup";
import "styles/balances-page/assets/AssetRow.css";

export default class AssetRow extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            sendOpen: false,
            receiveOpen: false
        }
        this.toggleSend = this.toggleSend.bind(this);
        this.toggleReceive = this.toggleReceive.bind(this);
        this.truncatePubAddr = this.truncatePubAddr.bind(this);
    }


    toggleSend() {
        this.setState({
            sendOpen: !this.state.sendOpen,
            receiveOpen: false
        });
    }

    toggleReceive () {
        this.setState({
            receiveOpen: !this.state.receiveOpen,
            sendOpen: false
        });
    }

    renderActionButtons() {
        return(
            <div className={"asset-table-action-buttons"}>
                <button className={"asset-table-action-button"} onClick={this.toggleSend}>
                    <span> Send </span>
                    <Send size="small" style={{verticalAlign: "middle"}}/>
                </button>
                <button className={"asset-table-action-button"} onClick={this.toggleReceive}>
                    <span> Receive </span>
                    <DownloadOption size="small" style={{verticalAlign: "middle"}}/>
                </button>
            </div>
        );
    }

    truncatePubAddr(issuer) {
        let length = issuer.length;
        return issuer.substr(0,4) + "..." + issuer.substr(length-4, length);
    }

    render() {
        return (
            <tr>
                <td className={"asset-cell"}>{this.props.name}</td>
                <td className={"asset-cell"} data-tip="" data-for={"issuerTip" + this.props.id}>{this.truncatePubAddr(this.props.issuer)} </td>
                <td className={"asset-cell"}>{this.props.balance}</td>
                <td className={"asset-cell"}>{this.props.id}</td>
                <td> {this.renderActionButtons()}</td>
                <ReactTooltip id={"issuerTip" + this.props.id} place={"bottom"} effect={"solid"}>
                    {this.props.issuer}
                </ReactTooltip>
                {this.state.sendOpen
                    ? <div className={"send-popup"}>
                        <h1 style={{fontFamily: "Roboto", fontSize: "18px", paddingLeft: "44px"}}>
                            {this.props.name}: {this.props.balance}
                        </h1>
                        <SendPopup boxType={this.props.name} fromAddress={this.props.currentAddress}
                                   toggleSend={this.toggleSend}  online={this.props.online} currentBalance={this.props.balance}/>
                    </div>
                    : null}
                {this.state.receiveOpen ?
                    <div className={"send-popup"} style={{width: "900px"}}>
                        <h1 style={{fontFamily: "Roboto", fontSize: "18px", paddingLeft: "44px"}}>
                            {this.props.name}: {this.props.balance}
                        </h1>
                        <ReceivePopup  publicAddress={this.props.currentAddress} toggleReceive={this.toggleReceive}/>
                    </div>
                    : null}
            </tr>
        )

    }
}

AssetRow.propTypes = {
    name: PropTypes.string.isRequired,
    balance: PropTypes.number.isRequired,
    issuer: PropTypes.string.isRequired,
    id: PropTypes.string.isRequired,
    currentAddress: PropTypes.string.isRequired,
    online: PropTypes.bool.isRequired
}