import './WalletApp.css';
import React from "react";
import {WalletSummary} from "./components/balances-page/WalletSummary"
import TopBar from "./components/full-page/TopBar";
import LeftMenuBar from "./components/full-page/LeftMenuBar";
import {KeysPage} from "./components/keys-page/KeysPage";

const assetInfo1 = {
    boxType: "poly",
    balance: 10000
};

const assetInfo2 = {
    boxType: "arbit",
    balance: 10000
};

const assets = [assetInfo1, assetInfo2];
const publicAddress =  "3mBVXE3fuVfu1MBmMsBiRd6y9XokiVV591N65tBfA3FEvmqWB8";

const keyfile2asset1 = {
    boxType: "poly",
    balance: 5000
}

const keyfile2asset2 = {
    boxType: "arbit",
    balance: 75000
}
const assets2 = [keyfile2asset1, keyfile2asset2];
const publicAddress2 = "4EoSC4YmTm7zoPt5HDJU4aa73Vn2LPrmUszvggAPM5Ff3R1DVt";

/**
 * @type {Map<string, object>}
 * Map: publicAddress -> keyfile info (name, password, list of assets)
 */
let keyFiles = new Map();
keyFiles.set(publicAddress, {name: "KeyFile 1", password: "password", assets: assets});
keyFiles.set(publicAddress2, {name: "KeyFile 2", password: "password2", assets: assets2});


export class WalletApp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            walletTabOpen: false,
            expandedSideBar: true,
            myBalancesOpen: true,
            currentKeyFile: publicAddress,
            online: true
        }
        this.openWalletMenu = this.openWalletMenu.bind(this);
        this.toggleMenu = this.toggleMenu.bind(this);
        this.openBalances = this.openBalances.bind(this);
        this.openKeys = this.openKeys.bind(this);
        this.switchKeyFile = this.switchKeyFile.bind(this);
        this.toggleConnection = this.toggleConnection.bind(this);
    }

    //toggle between user connected to internet or not (offline or online computer)
    toggleConnection() {
        this.setState({online: !this.state.online});
    }

    //opens dropdown below "Wallet" on side bar
    openWalletMenu() {
        this.setState({walletTabOpen: !this.state.walletTabOpen});
    }

    //Expands or collapses left side-bar menu
    toggleMenu() {
        this.setState({expandedSideBar: !this.state.expandedSideBar});
    }

    //opens balances page
    openBalances() {
        this.setState({myBalancesOpen: true});
    }

    //closes balances page and opens keys page
    openKeys() {
        this.setState({myBalancesOpen: false});
    }

    //lock current keyfile and unlock new keyfile
    switchKeyFile(pubAddr) {
        if (this.state.currentKeyFile !== pubAddr) {
            this.setState({currentKeyFile: pubAddr})
        }
    }

    render() {
        const polysBalance = keyFiles.get(this.state.currentKeyFile).assets.filter(asset => asset.boxType === "poly")
            .reduce(function (a, b) {return a + b.balance}, 0);

        const arbitsBalance = keyFiles.get(this.state.currentKeyFile).assets.filter(asset => asset.boxType === "arbit")
            .reduce(function (a, b) {return a + b.balance}, 0);

        let page, leftMargin, openPageWidth;
        if (this.state.myBalancesOpen) {
            page = <WalletSummary publicAddress={this.state.currentKeyFile} assets={keyFiles.get(this.state.currentKeyFile).assets} online={this.state.online}/>
        }else{
            page = <KeysPage  switchKeyFiles={this.switchKeyFile} keyFiles={keyFiles} currentKeyFile={this.state.currentKeyFile}/>
        }
        if (this.state.expandedSideBar) {
            leftMargin = "256px";
            openPageWidth = "80vw";
        }else{
            leftMargin = "60px";
            openPageWidth = "94vw";
        }

        return (
            <div className={"page"} style={{marginLeft: leftMargin}}>
                <div className={"open-page"} style={{width: openPageWidth}}>
                    <TopBar toggleMenu={this.toggleMenu} networkType={"Toplnet"} online={this.state.online} toggleConnection={this.toggleConnection}/>
                    {page}
                </div>
                {<LeftMenuBar myBalancesOpen={this.state.myBalancesOpen} walletTabOpen={this.state.walletTabOpen}
                              expandedSideBar={this.state.expandedSideBar} openWalletMenu={this.openWalletMenu}
                              openBalances={this.openBalances} openKeys={this.openKeys} polysBalance={polysBalance} arbitsBalance={arbitsBalance}/>}
            </div>
        );
    }
}

