import React from "react";
import arbit from "images/arbitcoin.png";
import poly from "images/poly.jpeg";
import "styles/balances-page/Balance.css"
import {Send, DownloadOption} from "grommet-icons";
import SendPopup from "./send/SendPopup";
import ReceivePopup from "./ReceivePopup";
import PropTypes from 'prop-types';

/**
 * Props:
 *  boxType: string
 *  balance: number
 *  publicAddress: string
 */

export class Balance extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sendOpen: false,
            receiveOpen: false
        }
        this.toggleSend = this.toggleSend.bind(this);
        this.toggleReceive = this.toggleReceive.bind(this);
    }

    toggleSend() {
        this.setState({
            sendOpen: !this.state.sendOpen,
            receiveOpen: false
        });
    }

    toggleReceive () {
        this.setState({
            receiveOpen: !this.state.receiveOpen,
            sendOpen: false
        });
    }

    render() {
        let icon, amount, sendPopup, receivePopup;
        if (this.props.boxType === "arbit") {
            icon = <img className={"icon"} src={arbit}  alt={"ARbit icon"}/>
            amount = <span className={"amount"}> {this.props.balance.toLocaleString()} ARB </span>
        }else if (this.props.boxType === "poly") {
            icon = <img className={"icon"} src={poly}  alt={"Poly icon"}/>
            amount = <span className={"amount"}> {this.props.balance.toLocaleString()} POLY </span>
        }
        let sendStyle = "action-button-closed"
        let receiveStyle = "action-button-closed"
        if (this.state.sendOpen) {
            sendStyle = "action-button-opened"
            sendPopup = <SendPopup boxType={this.props.boxType} fromAddress={this.props.publicAddress}
                                   toggleSend={this.toggleSend} online={this.props.online} currentBalance={this.props.balance}/>
        }
        if (this.state.receiveOpen) {
            receiveStyle = "action-button-opened"
            receivePopup = <ReceivePopup publicAddress={this.props.publicAddress} toggleReceive={this.toggleReceive}/>
        }
        return (
            <div className={"balance-row"}>
                {icon}
                {amount}
                <div className={"action-buttons"}>
                    <button className={sendStyle} onClick={this.toggleSend}>
                        <span> Send </span>
                        <Send style={{verticalAlign: "middle"}}/>
                    </button>
                    <button className={receiveStyle} onClick={this.toggleReceive}>
                        <span> Receive </span>
                        <DownloadOption style={{verticalAlign: "middle"}}/>
                    </button>
                </div>
                {sendPopup}
            {receivePopup}
            </div>
        )
    }
}

Balance.propTypes = {
    boxType: PropTypes.string.isRequired,
    balance: PropTypes.number.isRequired,
    publicAddress: PropTypes.string.isRequired,
    online: PropTypes.bool.isRequired
}