import React from "react";
import PropTypes from "prop-types";

export default class SendRecipient extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            addressValue: "",
            amount: null
        }
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value});
        if (event.target.name === "addressValue") {
            this.props.saveNewInfo(
                {address: event.target.value, amount: this.state.amount},
                this.props.index
            )
        } else if (event.target.name === "amount") {
            this.props.saveNewInfo(
                {address: this.state.addressValue, amount: parseInt(event.target.value)},
                this.props.index
            )
        }
        if (this.state.addressValue === "" || this.state.amount <= 0) {
            this.setState({showError: true});
        }
    }

    render() {
        let currency;
        if (this.props.boxType === "poly") {
            currency = "POLY"
        }else if (this.props.boxType === "arbit") {
            currency = "ARB"
        }

        let addressStyle = "address-box";
        let amountStyle = "amount-box";
        let warningMsg = "This field is required.";
        if (this.state.showError) {
            if (this.state.addressValue === "") {
                addressStyle = "address-box-warning";
            }
            if (this.state.amount <=0) {
                amountStyle = "amount-box-warning";
            }
        }

        return(
            <div style={{borderBottomStyle: "solid", borderColor: "white"}}>
                <div className={"field"} style={{marginTop: "0px"}}>
                    <span> Send to: </span>
                    <input type="text" name={"addressValue"} className={addressStyle} placeholder={"Enter address"} value={this.state.toAddress}
                           onChange={this.handleChange}/>
                    {(addressStyle === "address-box-warning") ? <span className={"error-msg"}> This field is required. </span> : null}
                </div>

                <div className={"field"} style={{marginTop: "10px", marginBottom: "10px"}}>
                    <span> Amount: </span>
                    <input type="number" name={"amount"} className={amountStyle} placeholder={"0"} value={this.state.amount}
                           onChange={this.handleChange}/>
                    <span className={"currency"}> {currency} </span>
                    {(amountStyle === "amount-box-warning") ? <span className={"error-msg"}> {warningMsg} </span> : null}
                    {(this.props.minusSign) ?
                        <button className={"text-button"} style={{float: "right", marginRight: "10px", lineHeight: "50px"}}
                                onClick={() => this.props.removeFunc(this.props.index)}>
                            Remove Recipient
                        </button>
                        : null}
                </div>
            </div>
        );
    }
}

SendRecipient.propTypes = {
    boxType: PropTypes.string.isRequired,
    minusSign: PropTypes.bool.isRequired,
    index: PropTypes.number.isRequired,
    removeFunc: PropTypes.func.isRequired,
    saveNewInfo: PropTypes.func.isRequired
}