import React from 'react';
import PropTypes from "prop-types";
import {FormAdd} from "grommet-icons";
import CreateAssetPopup from "./CreateAssetPopup";
import AssetRow from "./AssetRow";
import "styles/balances-page/assets/AssetTable.css"
import "styles/ToplButtons.css";

export class AssetTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showAssetPopup: false,
            assetsList: this.props.assetsList,
            sendOrReceiveOpen: false
        }
        this.togglePopup = this.togglePopup.bind(this);
        this.addAsset = this.addAsset.bind(this);
    }

    addAsset(newAsset) {
        this.setState({
            assetsList: this.state.assetsList.concat(newAsset)
        });
        this.togglePopup();
    }

    renderTable() {
        return this.state.assetsList.map((asset) => {
            const{name, balance, issuer, id} = asset;
            return <AssetRow name={name} balance={balance} issuer={issuer} id={id} currentAddress={this.props.currentAddress} online={this.props.online}/>
        })
    }

    capitalize (string) {
        if (typeof string !== 'string') return '';
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    renderTableHeader() {
        let header = Object.keys(this.state.assetsList[0]);
        return header.map((key) => {
                return <th className={"table-header"}>{this.capitalize(key)}</th>
        })
    }

    togglePopup() {
        this.setState({showAssetPopup: !this.state.showAssetPopup});
    }

    render() {
        return (
            <div style={{overflow: "auto"}}>
                <table className={"other-assets"}>
                    <tr>
                        {this.renderTableHeader()}
                        <th className={"table-header-actions"}> Actions </th>
                    </tr>
                    <tbody>{this.renderTable()}</tbody>
                </table>
                {this.state.showAssetPopup ? <CreateAssetPopup  currentAddress={this.props.currentAddress} addAsset={this.addAsset} toggle={this.togglePopup}/>
                : <button className={"round-blue-button"} style={{left: "36%", width: "156px", position: "relative", marginTop: "20px"}} onClick={this.togglePopup}>
                        <FormAdd color={"white"} thickness={"large"} style={{height: "25px", verticalAlign: "middle"}}/>
                        Create Asset
                    </button>}
            </div>
        )
    }
}

AssetTable.propTypes = {
    assetsList: PropTypes.arrayOf(PropTypes.shape({
        name: PropTypes.string,
        issuer: PropTypes.string,
        balance: PropTypes.number,
        id: PropTypes.string
    })).isRequired,
    currentAddress: PropTypes.string.isRequired,
    online: PropTypes.bool.isRequired
}