import React from "react";
import PropTypes from "prop-types";
import 'styles/keys-page/KeyCard.css';
import "styles/ToplButtons.css";
import {Unlock, Lock} from "grommet-icons";
import UnlockPopup from "./UnlockPopup";

/**
 * props:
 * publicAddress: string,
 * assets: array assets (boxType, balance)
 */
export default class KeyCard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showUnlockPopup: false
        }
        this.toggleUnlockPopup = this.toggleUnlockPopup.bind(this);
        this.unlockKeyFile = this.unlockKeyFile.bind(this);
    }

    toggleUnlockPopup() {
        this.setState({
            showUnlockPopup: !this.state.showUnlockPopup
        });
    }

    unlockKeyFile() {
        this.props.switchKeyFiles(this.props.publicAddress);
    }

    renderBalances() {
        const polysBalance = this.props.keyFileInfo.assets.filter(asset => asset.boxType === "poly")
            .reduce(function (a, b) {return a + b.balance}, 0);

        const arbitsBalance = this.props.keyFileInfo.assets.filter(asset => asset.boxType === "arbit")
            .reduce(function (a, b) {return a + b.balance}, 0);


        return (
            <ul style={{listStyleType: "none"}}>
                <li style={{paddingBottom: "10px"}}> {polysBalance} POLY </li>
                <li style={{paddingBottom: "10px"}}> {arbitsBalance} ARB </li>
            </ul>
        );
    }

    render() {
        if (this.props.unlocked) {
            return (
                <div className={"card-outline"} style={{backgroundColor: "#26A69A", color: "#FFFFFF"}}>
                    <h1 className={"wallet-header"}>
                        {this.props.keyFileInfo.name}
                        <Unlock size={"large"} color={"#FFFFFF"} style={{paddingLeft: "5px", float: "right"}}/>
                    </h1>
                    {this.renderBalances()}
                    {this.state.showUnlockPopup ? <UnlockPopup walletName={this.props.keyFileInfo.name} toggle={this.toggleUnlockPopup} unlockKeyFile={this.unlockKeyFile} password={this.props.keyFileInfo.password}/> : null}
                </div>
            )
        }else {
            return (
                <div className={"card-outline"}>
                    <h1 className={"wallet-header"}>
                        {this.props.keyFileInfo.name}
                        <Lock size={"large"} color="black" style={{paddingLeft: "5px", float: "right"}}/>
                    </h1>
                    {this.renderBalances()}
                    <button className={"round-blue-button"} style={{position: "relative", left: "20%", width: "60%",
                        fontSize: "18px", height: "40px", borderRadius: "30px"}} onClick={this.toggleUnlockPopup}>
                        Unlock
                    </button>
                    {this.state.showUnlockPopup ?
                        <UnlockPopup walletName={this.props.keyFileInfo.name} toggle={this.toggleUnlockPopup}
                                     unlockKeyFile={this.unlockKeyFile}
                                     password={this.props.keyFileInfo.password}/> : null}
                </div>
            )
        }
    }
}

KeyCard.propTypes = {
    publicAddress: PropTypes.string.isRequired,
    keyFileInfo: PropTypes.shape({
        name: PropTypes.string,
        password: PropTypes.string,
        assets: PropTypes.arrayOf(PropTypes.shape({
            boxType: PropTypes.string,
            balance: PropTypes.number
        }))
    }).isRequired,
    switchKeyFiles: PropTypes.func.isRequired,
    unlocked: PropTypes.bool
}

KeyCard.defaultProps = {
    unlocked: false
}